@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&family=Nunito:wght@300;400;700&display=swap);
body {
    margin: 0;
    min-height: 100%;
    padding: 0;
    background-size: 100%;
    font-family: 'Lato';
  }

  ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  a {
    text-decoration: none;
  }


.navbar {
    display: grid;
    grid-template-columns: 1fr;
    align-items: center;
    background-image: linear-gradient(to right, rgb(0, 140, 255), rgb(0, 6, 34) );
    position: fixed;
    top: 0;
    width: 100%;
    height: 100px;
    
  }

  .navbar-nav {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    list-style-type: none;
    margin: 0;
    max-width: 100%;
    height: 100%;
  }

  .navlink {
    color: #edf1f2;
    font-size: 22px;
    font-weight: bold;
    /* display: block; */
    text-align: center;
    padding: 20px;
    margin-right: 1.5em;
    transition: 0.5s;
    width: 100%;
  }

  .navlink:hover {
    background-color: #008299;
  }

header {
    height: 100vh;
    background: 
    linear-gradient(
      rgba(0, 0, 0, 0.5),
      rgba(0, 0, 0, 0.5)
    ), url(/react-dunbar-pages/static/media/dunbaraerial.bc9fde7b.jpg);
    background-position: center; 
    background-repeat: no-repeat; 
    background-size: cover;
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-top: 3em;
  }
  
  .title {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
    text-align: center;  
  }

  h1 {
    text-transform: uppercase;
    margin: 0;
    font-size: 4rem;
    white-space: nowrap;
    font-weight: bold;
  }
  h2 {
      margin: 0;
      font-size: 1.5rem;
      padding-top: 3em;
    }
  
  .home-information {
    margin-top: 0.5em;
    margin-bottom: 0.5em;
    margin-left: 1em;
  }
  
  .homepage-body-text {
    line-height: 1.5;
    font-size: 20px;
    text-align: justify;
    font-family: 'Nunito';
    margin-top: 1.5em;
    margin-left: 2em;
    margin-right: 5em;
  }

  .nav {
    display: grid;
    grid-template-columns: 1fr;
    align-items: center;
    justify-self: center;
    width: 100%;
  }

  .menu-banner-style {
    display: flex;
    justify-content: center;
    align-items: center;
    list-style-type: none;
  
  }
  
  .menu-home {
    box-shadow: 0 2px 2px 0 #00000033;
    max-width: 320px;
    text-align: center;
    padding: 1em;
    background-color: #f8f8ff;
    transition: 0.5s;
    margin: 1em;
  
  }

  .menu-home:hover {
    background-color: #b9f2fc;
    box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
  }
  
  .menu-home img {
    width: 280px;
    height: 200px;
    border: 1px solid black;
    border-radius: 20px 50px;
    padding: 10px;
    margin-left: auto;
    margin-right: auto;
    
  }

  .menu-title-home {
    background-color: black;
    color: white;
    font-size: 26px;
    font-family: "Nunito";
    padding: 0.3em;
    border: solid white;
    margin-top: 0.5em;

  }

  .footer {
    /* padding: 0; */
    background-color: #00144b;
    background-image: linear-gradient(to right, rgb(0, 140, 255), rgb(0, 6, 34) );

    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
  
    margin-top: 50px;
  }
  
  .footer-nav {
    max-width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;

    margin: 0;
   
  }

  .footer-text {
    
    color: #edf1f2;
    font-size: 20px;
    font-weight: bold;
    margin-left: 2em;
    

  }

  .social-footer, img {
    height: 50px;
    width: 50px;
    justify-self: flex-end;
    padding: 25px;
    


    /* padding: 0.5em;
    margin-left: 0.8em;
    
    */
    
  }

